const InputGroup = Input.Group;
const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const redirectPT = "/dashboard/purchases/purchase-returns";
const redirectPR = "/dashboard/purchases/purchase-receipts";
const redirectPI = "/dashboard/purchases/purchase-invoices";

const expandedRowRender = data => {
  const columns = [
    {
      title: "No",
      dataIndex: "index_no",
      width: "2%"
    },
    {
      title: getLanguage({ id: "items.code" }),
      dataIndex: "item_code",
      width: "15%"
    },
    {
      title: getLanguage({ id: "itemName" }),
      dataIndex: "item_name",
      width: "30%",
      render: text => {
        if (text && text.length > 50) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 50) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "returnedQty" }),
      dataIndex: "return_qty",
      width: "10%",
      align: "right",
      render: text => <NumberFormat value={parseFloat(text)} />
    },
    {
      title: getLanguage({ id: "price" }) + " (IDR)",
      dataIndex: "item_price", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => {
        if (row.currency_rate) {
          return (
            <NumberFormat
              value={parseFloat(row.currency_rate) * parseFloat(text)}
            />
          );
        } else {
          return <NumberFormat value={parseFloat(text)} />;
        }
      }
    },
    {
      title: getLanguage({ id: "sidebar.tax" }) + " (IDR)",
      dataIndex: "sub_total_tax", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => {
        if (row.currency_rate) {
          return (
            <NumberFormat
              value={parseFloat(row.currency_rate) * parseFloat(text)}
            />
          );
        } else {
          return <NumberFormat value={parseFloat(text)} />;
        }
      }
    },
    {
      title: getLanguage({ id: "subTotal" }) + " (IDR)",
      dataIndex: "return_sub_total", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => {
        if (row.currency_rate) {
          return (
            <NumberFormat
              value={parseFloat(row.currency_rate) * parseFloat(text)}
            />
          );
        } else {
          return <NumberFormat value={parseFloat(text)} />;
        }
      }
    }
  ];

  const dataSource = data;
  return (
    <div>
      <strong>
        <IntlMessages id="purchaseInvoices.importPR.itemList" />
      </strong>

      <Table
        rowKey={record => record.index_no}
        size="small"
        columns={columns}
        dataSource={dataSource}
        pagination={false}
        style={{ marginBottom: "5px", marginTop: "5px" }}
      />
    </div>
  );
};

class PurchaseReturnDetailReport extends Component {
  state = {
    filter: {
      id_supplier: null,
      id_location: null,
      status: null,
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      sort_by: "return_date",
      sort_type: "asc"
      // pagination: null
    },
    search: null,
    search_by: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getPurchaseReturnDetailReports } = this.props;
    getPurchaseReturnDetailReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.purchaseReturnDetailReport.error) {
      notification("error", this.props.purchaseReturnDetailReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.purchaseReturnDetailReport.success) {
      notification("success", this.props.purchaseReturnDetailReport.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { search_by, search } = this.state;
    let string = "";
    if (search_by && search) {
      string = "search_by=" + search_by + "&search=" + search;
    } else if (!search_by && search) {
      string =
        "search_by=" +
        this.props.purchaseReturnDetailReport.pt_detail_search_filter[0]
          .filter_value +
        "&search=" +
        search;
    } else
      string =
        "search_by=" +
        this.props.purchaseReturnDetailReport.pt_detail_search_filter[0]
          .filter_value;
    const { getPurchaseReturnDetailReports } = this.props;
    let filter = this.filterValue();

    getPurchaseReturnDetailReports(1, string, filter, this.state.filter);
  };

  //handle Search By
  handleSearchBy = e => {
    this.setState({ search_by: e });
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getPurchaseReturnDetailReports } = this.props;
    this.setState({
      filter: {
        id_supplier: null,
        id_location: null,
        status: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "return_date",
        sort_type: "asc"
        // pagination: null
      }
    });
    this.setState({ search: null, search_by: null });
    getPurchaseReturnDetailReports();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else if (name === "id_location") {
      this.setState({
        filter: {
          ...this.state.filter,
          id_location: value,
          id_supplier: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.status) {
      string
        ? (string += "&status=" + filter.status)
        : (string += "status=" + filter.status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_supplier) {
      string
        ? (string += "&id_supplier=" + filter.id_supplier)
        : (string += "id_supplier=" + filter.id_supplier);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getPurchaseReturnDetailReports } = this.props;
    //const { current_page } = this.props.purchaseReturnDetailReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getPurchaseReturnDetailReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getPurchaseReturnDetailReports } = this.props;
    this.setState({
      filter: {
        id_supplier: null,
        id_location: null,
        status: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "return_date",
        sort_type: "asc"
        // pagination: null
      }
    });
    this.setState({ search: null, search_by: null });
    getPurchaseReturnDetailReports();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activatePurchaseReturnReport(id);
    } else {
      this.props.deactivatePurchaseReturnReport(id);
    }
  };

  // // Handle pagination
  // handlePageChange = page => {
  //   const { getPurchaseReturnDetailReports } = this.props;
  //   const { search, filter } = this.props.purchaseReturnDetailReport;
  //   getPurchaseReturnDetailReports(page, search, filter);
  // };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "purchaseReturns.purchaseReturnNo" }),
        dataIndex: "purchase_return_no",
        rowKey: "purchase_return_no",
        width: "10%",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "pending";
              break;
            case 2:
              className = "processed";
              break;
            case 3:
              className = "cancelled";
              break;
            default:
              className = "pending";
              break;
          }

          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <TransNo>
                <Link to={`${redirectPT}/update/${row.id_purchase_return}`}>
                  {text}
                </Link>
              </TransNo>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "purchaseReturnDate" }),
        dataIndex: "return_date",
        rowKey: "return_date",
        //width: "10%",
        date: true,
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "prpino" }),
        dataIndex: "transaction_type",
        rowKey: "transaction_type",
        width: "10%",
        render: (text, row) => {
          let redirect;

          if (text === 1) {
            redirect = redirectPR;
          } else if (text === 2) {
            redirect = redirectPI;
          }

          return (
            <div style={{ display: "flex", height: "20px" }}>
              <TransNo>
                <Link to={`${redirect}/update/${row.id_transaction}`}>
                  {row.transaction_no}
                </Link>
              </TransNo>
            </div>
          );

          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        //width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.supplier" }),
        dataIndex: "supplier_name",
        rowKey: "supplier_name",
        //width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "salesOrders.totalQty" }),
        dataIndex: "total_qty",
        rowKey: "total_qty",
        //width: "15%",
        align: "right",
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalDiscount" }) + " (IDR)",
        dataIndex: "total_discount",
        rowKey: "total_discount",
        //width: "15%",
        align: "right",
        render: (text, row) => {
          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalTax" }) + " (IDR)",
        dataIndex: "total_tax",
        rowKey: "total_tax",
        //width: "15%",
        align: "right",
        render: (text, row) => {
          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalAmount" }) + " (IDR)",
        dataIndex: "total_amount",
        rowKey: "total_amount",
        //width: "15%",
        align: "right",
        render: (text, row) => {
          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "5%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "processedBy" }),
        dataIndex: "processed_by",
        rowKey: "processed_by",
        width: "5%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status",
        rowKey: "status",
        width: "5%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "pending";
              display = getLanguage({ id: "pending" });
              break;
            case 2:
              className = "processed";
              display = getLanguage({ id: "processed" });
              break;
            case 3:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    const excelColumns = [
      {
        title: getLanguage({ id: "purchaseReturns.purchaseReturnNo" }),
        dataIndex: "purchase_return_no",
        rowKey: "purchase_return_no",
        //width: "15%",
        fixed: "left"
      },
      {
        title: getLanguage({ id: "purchaseReturnDate" }),
        dataIndex: "return_date",
        rowKey: "return_date",
        date: true
      },
      {
        title: getLanguage({ id: "prpino" }),
        dataIndex: "transaction_no",
        rowKey: "transaction_no"
        //width: "15%",
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name"
      },
      {
        title: getLanguage({ id: "sidebar.supplier" }),
        dataIndex: "supplier_name",
        rowKey: "supplier_name"
      },
      {
        title: getLanguage({ id: "salesOrders.totalQty" }),
        dataIndex: "total_qty",
        rowKey: "total_qty",
        //width: "15%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalDiscount" }) + " (IDR)",
        dataIndex: "total_discount",
        rowKey: "total_discount",
        //width: "15%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalTax" }) + " (IDR)",
        dataIndex: "total_tax",
        rowKey: "total_tax",
        //width: "15%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalAmount" }) + " (IDR)",
        dataIndex: "total_amount",
        rowKey: "total_amount",
        //width: "15%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by"
      },
      {
        title: getLanguage({ id: "processedBy" }),
        dataIndex: "processed_by",
        rowKey: "processed_by"
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status",
        rowKey: "status",
        //width: "10%",
        align: "center"
      },
      {
        title: getLanguage({ id: "items.code" }),
        dataIndex: "item_code",
        width: "15%"
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        width: "30%"
      },
      {
        title: getLanguage({ id: "returnedQty" }),
        dataIndex: "return_qty",
        width: "10%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "price" }) + " (IDR)",
        dataIndex: "item_price",
        width: "10%",
        align: "right",
        parse: true
      },
      // {
      //   title: "Discount (IDR)",
      //   dataIndex: "sub_total_discount", // dari PR
      //   width: "10%",
      //   align: "right",
      //   render: (text, row) => {
      //     return (parseFloat(row.currency_rate) * parseFloat(text))
      //       .toFixed(2)
      //       .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      //   }
      // },
      {
        title: getLanguage({ id: "sidebar.tax" }) + " (IDR)",
        dataIndex: "sub_total_tax", // dari PR
        width: "10%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "subTotal" }) + " (IDR)",
        dataIndex: "return_sub_total", // dari PR
        width: "10%",
        align: "right",
        parse: true
      }
    ];
    // Data setup
    // const { purchaseReturnDetailReport } = this.props;
    const {
      purchase_return_detail_reports,
      pt_detail_search_filter,
      locations,
      suppliers,
      filter,
      filter_select,
      loading,
      loadingForeign
    } = this.props.purchaseReturnDetailReport;
    //console.log(this.props.purchaseReturnDetailReport.base_currency); //console.log(this.state);

    let dataSource = [];
    let expandedRowKeys = [];
    let excelDataSource = [];
    if (
      purchase_return_detail_reports &&
      purchase_return_detail_reports.length > 0
    ) {
      // for table and print's datasource
      Object.keys(purchase_return_detail_reports).map((item, index) => {
        purchase_return_detail_reports[index].purchase_return_details.map(
          data =>
            (data.currency_rate =
              purchase_return_detail_reports[index].currency_rate)
        );
        return dataSource.push({
          ...purchase_return_detail_reports[index],
          key: index
        });
      });

      // for print's expandedRow
      Object.keys(purchase_return_detail_reports).map((item, index) => {
        return expandedRowKeys.push(index);
      });

      // for excel export
      purchase_return_detail_reports.map((item, index) => {
        let status;
        switch (item.status) {
          case 1:
            status = getLanguage({ id: "pending" });
            break;
          case 2:
            status = getLanguage({ id: "processed" });
            break;
          case 3:
            status = getLanguage({ id: "cancelled" });
            break;
          default:
            status = getLanguage({ id: "new" });
            break;
        }
        let child = {};
        // item.total_discount =
        //   parseFloat(item.currency_rate) * parseFloat(item.total_discount);
        // item.total_tax =
        //   parseFloat(item.currency_rate) * parseFloat(item.total_tax);
        // item.total_amount =
        //   parseFloat(item.currency_rate) * parseFloat(item.total_amount);
        item.purchase_return_details.map((data, i) => {
          child.item_price =
            parseFloat(item.currency_rate) * parseFloat(data.item_price);
          // data.sub_total_discount =
          //   parseFloat(item.currency_rate) *
          //   parseFloat(data.sub_total_discount);
          child.sub_total_tax =
            parseFloat(item.currency_rate) * parseFloat(data.sub_total_tax);
          child.return_sub_total =
            parseFloat(item.currency_rate) * parseFloat(data.return_sub_total);

          return excelDataSource.push({
            ...item,
            status,
            ...data,
            ...child
          });
        });
      });
    }

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    const fileName = getLanguage({
      id: "purchaseReturnDetailReports.pageHeader"
    });

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="purchaseReturnDetailReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={
                <IntlMessages id="purchaseReturnDetailReports.pageHeader" />
              }
              columns={columns}
              data={dataSource}
              filter={filter_select}
              defaultExpandAllRows={true}
              expandedRowKeys={expandedRowKeys}
              expandedRowRender={record =>
                expandedRowRender(record.purchase_return_details)
              }
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() =>
                (document.title = getFileName(fileName, null, false))
              }
              onAfterPrint={() => (document.title = siteConfig.documentTitle)}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "zoom: 0.8;" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              defaultExpandAllRows={true}
              expandedRowKeys={expandedRowKeys}
              expandedRowRender={record =>
                expandedRowRender(record.purchase_return_details)
              }
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              margin=".7cm"
              scale={0.4}
              title="purchaseReturnDetailReports.pageHeader"
            />

            <ExcelWrapper
              title="purchaseReturnDetailReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={excelColumns}
              data={excelDataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <InputGroup
              compact
              style={{
                alignItems: "center",
                display: "flex",
                marginRight: "1%"
              }}
            >
              <Select
                style={{ width: "40%" }}
                value={
                  this.state.search_by ||
                  (pt_detail_search_filter && pt_detail_search_filter.length > 0
                    ? pt_detail_search_filter[0].filter_value
                    : [])
                }
                placeholder={<IntlMessages id="select.searchBy" />}
                onChange={this.handleSearchBy}
              >
                {pt_detail_search_filter && pt_detail_search_filter.length > 0
                  ? pt_detail_search_filter.map(data => (
                      <Option key={data.filter_value} value={data.filter_value}>
                        {getLanguage({ id: data.filter_label })}
                      </Option>
                    ))
                  : null}
              </Select>
              <Search
                style={{ width: "60%" }}
                placeholder={getLanguage({ id: "keyword" })}
                onSearch={this.handleSearch}
                value={this.state.search}
                onChange={this.handleSearchValue}
              />
            </InputGroup>
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                disabled={
                  !permissions.includes("Purchase Return Detail Report")
                    ? true
                    : false
                }
                onClick={this.handleSearch.bind(this, this.state.search)}
                type="primary"
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="suppliers.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_supplier"
                      )}
                      value={
                        this.state.filter.id_supplier
                          ? this.state.filter.id_supplier
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        this.state.filter.id_location ? (
                          <IntlMessages id="select.supplier" />
                        ) : (
                          <IntlMessages id="noLocation" />
                        )
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      notFoundContent={
                        this.state.filter.id_location ? (
                          <Empty description={<IntlMessages id="noData" />} />
                        ) : (
                          <Empty
                            description={<IntlMessages id="noLocation" />}
                          />
                        )
                      }
                    >
                      {this.state.filter.id_location
                        ? suppliers.map((data, key) =>
                            data.id_location ===
                            this.state.filter.id_location ? (
                              <Option key={key} value={data.id_supplier}>
                                {`${data.supplier_code} (${data.supplier_name})`}
                              </Option>
                            ) : null
                          )
                        : null}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="purchaseInvoices.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>

                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="stockValuationReports.sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "return_date"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="return_date">
                            <IntlMessages id="purchaseReturnDetailReports.filter.date" />
                          </Option>
                          <Option key={2} value="purchase_return_no">
                            <IntlMessages id="purchaseReturns.purchaseReturnNo" />
                          </Option>
                          <Option key={3} value="location_name">
                            <IntlMessages id="sidebar.location" />
                          </Option>
                          <Option key={4} value="supplier_name">
                            <IntlMessages id="sidebar.supplier" />
                          </Option>
                          <Option key={5} value="total_amount">
                            <IntlMessages id="totalAmount" />
                          </Option>
                          <Option key={6} value="total_qty">
                            <IntlMessages id="totalQuantity" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "100%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <Row gutter={1} type="flex">
                      <Col span={12}>
                        <label>Status</label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "status"
                          )}
                          value={
                            this.state.filter.status
                              ? this.state.filter.status
                              : []
                          }
                          style={{ width: "95%" }}
                          placeholder="Status"
                          optionFilterProp="children"
                          filterOption={true}
                          // loading={this.props.location.loadingFilterSites}
                          allowClear={true}
                          //showArrow={false}
                        >
                          <Option value={1}>
                            {getLanguage({ id: "pending" })}
                          </Option>
                          <Option value={2}>
                            {getLanguage({ id: "processed" })}
                          </Option>
                          <Option value={3}>
                            {getLanguage({ id: "cancelled" })}
                          </Option>
                        </Select>
                      </Col>
                      <Col
                        span={8}
                        style={{
                          marginTop: "24px"
                        }}
                      >
                        <Button
                          disabled={
                            !permissions.includes(
                              "Purchase Return Detail Report"
                            )
                              ? true
                              : false
                          }
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clicksearchFilter" />} />
            ) : purchase_return_detail_reports.length === 0 &&
              !this.props.purchaseReturnDetailReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="purchaseReturnDetailReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    defaultExpandAllRows={true}
                    expandedRowRender={record =>
                      expandedRowRender(record.purchase_return_details)
                    }
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    purchaseReturnDetailReport: state.purchaseReturnDetailReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  PurchaseReturnDetailReport
);
